import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/auditTime';

/**
 * @hidden
 */
export const THROTTLE_MS = 1000 / 60;

/**
 * @hidden
 */
export class Change {
  constructor(public key: string, public value: any) {
  }
}

/**
 * @hidden
 */
@Injectable()
export class ConfigurationService {
  public onChange$: Observable<any>;
  public store: any = {};

  protected source: BehaviorSubject<any> = new BehaviorSubject<any>({});

  constructor() {
    this.initSource();
  }

  protected initSource(): void {
    this.onChange$ = this.source.asObservable().auditTime(THROTTLE_MS);
  }

  public push(store: any): void {
    this.store = store;
    this.source.next(this.store);
  }

  public notify(change: Change): void {
    let store = this.store;
    let parts = change.key.split('.');
    let key = parts.shift();

    while (parts.length > 0) {
      store = store[key] = store[key] || {};
      key = parts.shift();
    }

    store[key] = change.value;
    this.source.next(this.store);
  }
}



